.App {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Movie-container{
  width: 100%;
  display: flex;
  flex-wrap:wrap;
  justify-content: center;
  align-items: center;
  gap:20px;
}

.Movie-wrapper{
  width: 300px;
  background-color: antiquewhite;
  color: black;
}
.Movie-search{
  height: 48px;
  font: size 24px;
  margin-bottom: 32px;
  padding:16px;
  font-weight: bold;
  width: 475px;
}

.Movie-title{
  height: 60px;
  margin: 20px;
}
img.Movie-image{
  width: 80%;
}
